<template>
  <el-form :model="form" :rules="rules" ref="form">
    <el-form-item prop="pass">
      <el-input
        v-model.trim="form.pass"
        :placeholder="$t('login.new_password')"
        type="password"
        clearable
        show-password
        @change="handlePasswordChange" />
    </el-form-item>

    <el-form-item prop="passwordConfirm">
      <el-input
        v-model.trim="form.passwordConfirm"
        :placeholder="$t('login.confirm_password')"
        type="password"
        clearable
        show-password />
    </el-form-item>
  </el-form>
</template>

<script>
import { mixinValidateForm, mixinCheckPasswordAsPrevious } from '@/mixins/form-validation';

import validator from '@/utils/validators/rules-validation';
import validPassword from '@/utils/validators/valid-password';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';

export default {
  mixins: [mixinValidateForm, mixinCheckPasswordAsPrevious],
  data() {
    return {
      form: {},
      rules: {
        pass: [
          validator.requirement(),
          // ...validator.charLengthRange(6, 20),
          // validator.charTypeNotOnlyDigitOrLatin(),
          validator2ElemRule(validPassword),
        ],
      },
    };
  },
};
</script>
