<template>
  <div class="password-reset-index">
    <img
      v-if="currentComponent !== 'PageSuccessMessage'"
      :src="logo"
      class="logo"
      :class="{'logo-anta':appName === 'anta'}"
      :alt="$t('login.index.logo_alt')"
      @click="$router.push({ name: 'Login' })" />
    <component
      @verify-email-success="handleVerifySuccess"
      @submit="handleResetPassword"
      :is="currentComponent" />
    <BusinessFootNote v-if="appName === 'anta'"/>
  </div>
</template>

<script>
import mixinLoading from '@/mixins/loading';
import { updateUserPasswordByVerificatioCode } from '@/api/account';
import envs from '@/utils/envs';
import BusinessFootNote from '../components/BusinessFootNote.vue';
import PageVerifyCode from './components/PageVerifyCode';
import PagePasswordReset from './components/PagePasswordReset';
import PageSuccessMessage from './components/PageSuccessMessage';


export default {
  components: {
    PageVerifyCode,
    PagePasswordReset,
    PageSuccessMessage,
    BusinessFootNote,
  },
  mixins: [mixinLoading],
  data() {
    return {
      currentComponent: 'PageVerifyCode',
      form: {},
    };
  },
  computed: {
    logo() {
      return envs.VUE_APP_COOPERATION === 'anta' ? require('@/assets/img/login/logo-anta.png')
        : require('@/assets/img/login/logo-register.png');
    },
    appName() {
      return envs.VUE_APP_COOPERATION;
    },
  },
  methods: {
    handleVerifySuccess(email, verifyCode) {
      Object.assign(this.form, { account: email.toLowerCase(), verifyCode });
      this.currentComponent = 'PagePasswordReset';
    },
    async handleResetPassword(payload) {
      this.startLoading(); // 打开Loading状态
      Object.assign(this.form, payload);

      try {
        await updateUserPasswordByVerificatioCode(this.form);
        this.currentComponent = 'PageSuccessMessage';
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.endLoading(); // 关闭Loading状态
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.password-reset-index {
  position: relative;
  padding: 0 40px;
  width: 400px;
  min-height: 480px;

  .logo {
    margin: 60px 0 40px -16px;
    height: 24px;
    cursor: pointer;
    &.logo-anta {
      height: 19px;
      margin: 64px 0 40px 0;
    }
  }
}
</style>
