<template>
  <div class="verify-code-page">
    <h1 class="title">{{ $t('login.retrieve_password') }}</h1>

    <FormEmailVerifyCodeByStep
      type="password"
      mode="dynamicVerifyEmail"
      checkScope="checkMainAccount"
      ref="formEmailVerifyCode"
      @validate="submit" />

    <!-- <el-button
      class="submit"
      type="primary"
      @click="handleNext">{{ $t('login.next_step') }}</el-button> -->

    <back-login class="back-login-wrap" />
  </div>
</template>

<script>
import FormEmailVerifyCodeByStep from '@/components/form/whole-form/FormEmailVerifyCodeByStep';

import mixinLoading from '@/mixins/loading';

import { checkVerificationCodeMatching } from '@/api/email';
import BackLogin from '../../components/BackLogin';

export default {
  components: {
    FormEmailVerifyCodeByStep,
    BackLogin,
  },
  mixins: [mixinLoading],
  methods: {
    async submit(form) {
      this.startLoading(); // 打开Loading状态
      try {
        await checkVerificationCodeMatching({ ...form, type: 3 });
        this.$emit('verify-email-success', form.email, form.verifyCode);
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.endLoading(); // 关闭Loading状态
      }
    },
    // handleNext() {
    //   this.$refs.formEmailVerifyCode.validateForm();
    // },
  },
};
</script>

<style lang="scss" scoped>
