  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;

  .title {
    margin-bottom: 20px;
    font-size: 16px;
    text-align: left;
  }
  .submit {
    margin: 10px 0 20px;
  }
  .back-login-wrap {
    margin-top: 20px;
  }
}
